  export default {
    data() {
      return {
        address_flag: false,
        slots: slots,
        address: '',
        popupVisible: false
      }
    },
    props: {
      confirmText: {
        default: '确定',
        type: String
      },
      cancelText: {
        default: '取消',
        type: String
      }
    },
    methods: {
      initAddress() {
        this.slots[0].values = address.filter((item, index) => {
          if (item.apid === 0) {
            return item;
          }
        });
      },
      onValuesChange(picker, values) {
        if (values[0]) {
          this.slots[1].values = address.filter((item, index) => {
            if (item.apid === values[0].aid) {
              return item;
            }
          });
        }
        if (values[1]) {
          this.slots[2].values = address.filter((item, index) => {
            if (item.apid === values[1].aid) {
              return item;
            }
          });
        }
        if (values[2]) {
          this.temp_addr = values[0].aname + ' ' + values[1].aname + ' ' + values[2].aname;
        }
      },
      open() {
        this.popupVisible = true
//        document.body.style.overflowY = 'hidden'
//        document.documentElement.style.overflowY = 'hidden'
//        document.documentElement.style.position = 'fixed'
      },
      close() {
        this.popupVisible = false
//        document.body.style.overflowY = ''
//        document.documentElement.style.overflowY = ''
//        document.documentElement.style.position = ''
      },
      confirm() {
        //传送消息
        this.$emit('addresschange', this.temp_addr)
        this.close()
      }
    },
    mounted() {
      this.initAddress()
    }
  }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  @import "../../common/sass/factory";

  .addressPicker-container {
    width: 100%;
    .mint-datetime-action {
      display: inline-block;
      width: 50%;
      text-align: center;
      line-height: 40px;
      font-size: 16px;
      color: $themeColorHover;
    }
    .picker-slot-wrapper, .picker-item {
      backface-visibility: hidden;
    }
    .picker-toolbar {
      border-bottom: solid 1px #eaeaea;
    }

    .address-picker {

    }

  }

</style>



